const UserTestingForSeniorCitizens = () => (
    <div className="container-fluid segment" id="user-testing-sc">
        <div className="container">
            <div class="row">
                <div className="col-md-12">
                    <h2 className="segment-title">User Testing Senior Citizens</h2>
                </div>
            </div>

            <div class="row">
                <div className="col-md-6 of-contain">
                    <img className="br-1" src="/static/mrs_laine.png"></img>
                </div>
                <div className="col-md-offset-1 col-md-5 ">
                We tested the senior citizen focused prototype on three participants at The Manors of Mosman and from this realised we needed to take somewhat of a concept pivot.  Due to poor views of AVs from senior citizens we shifted the focus of AV transport to the younger,  more open-minded volunteers.  Additionally, when 2/4 of the participants refused to use our prototype on mobile we realised it was worthless trying to design for the techno-phobic and instead focused on the senior citizens who are trying to integrate themselves by developing our wireframes on Ipad.                </div>
            </div>
        </div>
    </div>
)

export default UserTestingForSeniorCitizens;


